main {
  background-position-x: center !important;
  background-size: cover !important;
}

.level {
  padding: 20px;
  background-color: #334;
  border-radius: 20px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.level h1 {
  margin: 0px;
  text-align: center;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
}
.level h1 span {
  flex-shrink: 1;
}
.level h1 hr {
  background-color: #778;
  width: 20px;
  margin: 10px;
  flex-shrink: 0;
  margin-top: 12px;
}
.level hr {
  margin: 0px;
  background-color: #556;
  margin: 20px 0px;
}
.level .users {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 300px;
}
.level .users span {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
}
.level .users span * {
  margin: 0px;
  font-size: large;
}
.level .users span h3 {
  margin-bottom: 5px;
  font-size: x-large;
}
.level section {
  width: 100%;
  display: flex;
}
.level section iframe {
  height: 500px;
  border-radius: 10px;
  margin: 20px 0px;
  aspect-ratio: 16/9;
}
.level section .info {
  width: calc(100% - 40px);
  padding: 10px 20px;
  background-color: #223;
  border-radius: 10px;
  display: flex;
  flex-wrap: wrap;
  margin: 20px;
  margin-right: 0px;
}
.level section .info span {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  margin: 10px;
}
.level section .info span * {
  margin: 0px;
  font-size: large;
  text-align: center;
}
.level section .info span h3 {
  margin-bottom: 5px;
}

.records {
  padding: 20px;
  background-color: #334;
  border-radius: 20px;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 20px;
}
.records h1 {
  margin: 0px;
  text-align: center;
  margin-bottom: 20px;
}
.records .table {
  display: flex;
  flex-direction: column;
  width: calc(100% - 80px);
  background-color: #223;
  padding: 20px;
  border-radius: 10px;
}
.records .table a {
  display: flex;
  height: 50px;
  color: #dde;
  text-decoration: none;
  font-size: larger;
  border-radius: 5px;
}
.records .table a span {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
}
.records .table a .rank {
  width: 50px;
}
.records .table a .rank img {
  height: 30px;
}
.records .table a .holder {
  flex-grow: 1;
  justify-content: flex-start;
  overflow: hidden;
  text-overflow: ellipsis;
  flex-shrink: 1;
}
.records .table a .time {
  flex-grow: 1;
  justify-content: flex-end;
  font-family: "Droid Sans Mono", "Courier New", Courier, monospace;
}
.records .table a .time .ms {
  color: #778;
}
.records .table a .mobile {
  width: 50px;
  font-size: 30px;
}

.transparent {
  color: #fff;
}
.transparent .level {
  background-color: rgba(51, 51, 68, 0.4666666667);
  backdrop-filter: blur(8px) saturate(0.5);
}
.transparent .level hr {
  background-color: rgba(170, 170, 187, 0.5333333333);
}
.transparent .level section .info {
  background-color: rgba(0, 0, 17, 0.2666666667);
}
.transparent .records {
  background-color: rgba(51, 51, 68, 0.4666666667);
  backdrop-filter: blur(8px) saturate(0.5);
}
.transparent .records .table {
  background-color: rgba(0, 0, 17, 0.2666666667);
}
.transparent .records .table a {
  color: #fff;
}
.transparent .records .table a .ms {
  color: #99a;
}

body:not(.touch) main .table a:hover {
  background-color: #334;
}
body:not(.touch) main.transparent .table a:hover {
  background-color: rgba(238, 238, 255, 0.0666666667);
}

body.touch main .table a:active {
  background-color: #334;
}
body.touch main.transparent .table a:active {
  background-color: rgba(238, 238, 255, 0.0666666667);
}

@media screen and (max-width: 1200px) {
  .level section {
    flex-direction: column !important;
  }
  .level section iframe {
    width: 100%;
    height: unset !important;
  }
  .level section .info {
    margin: 0px !important;
  }
}
@media screen and (max-width: 800px) {
  .level section {
    flex-direction: column !important;
  }
  .level section .info {
    margin: 0px !important;
  }
  .records .table {
    width: calc(100% - 40px) !important;
  }
  .records .table a {
    height: 35px !important;
  }
  .records .table a .rank {
    width: 35px !important;
  }
  .records .table a .rank img {
    height: 20px !important;
  }
  .records .table a .time {
    margin-right: 5px;
  }
  .records .table a .mobile {
    display: none !important;
  }
}